.header {
  // padding: 10px 20px;
  // display: flex;
  // justify-content: space-around;
  padding-top: 10px;
  padding-bottom: 10px;

  box-shadow: 0px 4px 24px rgba(105, 105, 105, 0.1);

  @media screen and (min-width: 768px) {}
}

.header__conteiner {
  position: relative;
  display: flex;
  align-items: center;
}

.header__nav {
  display: flex;
  margin-left: auto;
  align-items: center;

  // width: 100%;
  @media screen and (max-width: 767px) {
    position: absolute;

    top: 50px;
    right: 20px;
    // padding: 16px;
    width: 130px;

    font-size: 16px;
    background-color: $main-background-color;
    box-shadow: 0px 4px 24px rgba(105, 105, 105, 0.1);
    border-radius: 30px 0px 30px 30px;
    overflow: hidden;

    // outline: 1px solid orange;
    // display: none;
    opacity: 0;
    // position: absolute;

    // transform: translate(200%, -150%);
    transform: translateX(100%);
    // display: none;
    transition: transform $animations, opacity $animations;
    // transition: transform 1000ms linear, opacity 1000ms linear;
    // transition: transform 1000ms linear, opacity 1000ms linear;
  }
}

.header__nav.is-open {
  display: block;
  opacity: 1;
  // transform: translate(0, 0);
  transform: translateX(0);
}

.header__button {
  margin-left: 28px;

  @media screen and (max-width: 767px) {
    display: none;
  }
}

.header__button {
  @include button("header", "fill");
}